@import 'bourbon';
@import 'helpers/variables';

.profile-sidebar {
	.col-lg-2 {
		padding-right: 0; } }

.sidebar-wrapper {

	.sidebar-title {
		height: 32px;
		background: $main-color;
		color: #fff;
		font-size: rem(16);
		line-height: rem(32px);
		text-transform: uppercase;
		text-align: center;
		margin-bottom: 7px; }

	.sidebar-menu {
		background: #fff;
		box-shadow: $block-shadow;
		padding: 20px 10px;
		font-size: rem(14);
		line-height: rem(30px);
		text-transform: capitalize;
		a {
			color: $lightgrey; }

		.left-fixer {
			display: block;
			&:hover {
				background: rgba(33,150,243,0.05); }

			.left-icon {
				@include size(30px 25px);
				background: {
					image: url('../img/sprites/icons.png');
					repeat: no-repeat;
					position: 7px 7px; } }

			.right-count {
				color: $main-color;
				font-weight: 700; }

			.left-label {
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap; } } } }
