@import 'bourbon';
@import 'helpers/variables';

// СТИЛИ СЕКЦИЙ
.section-news {
	padding: 60px 0 45px;
	text-align: center;
	color: $body-text-color;
	font-size: rem(18);
	line-height: rem(31); }

.section-popular {
	padding: 0 0 85px;
	margin-top: -45px; }

.popular-header {
	margin-bottom: 40px; }

.popular-ladies-profile {
	position: relative;
	@include size(180px);
	perspective: 180px;
	margin-bottom: 15px;
	&:hover .profile-data {
		@include size(100%);
		@include position(absolute, 0 0 0 0);
		overflow: hidden;
		background: rgba(0,0,0,0.70);
		transform: scale(1,1); }

	.profile-image {
		position: relative;
		@include size(100%);
		margin: 0 auto;
		&:after {
			content: '';
			@include position(absolute, null 15px 12px null);
			z-index: 5;
			@include size(20px);
			background: $online;
			border-radius: 50%;
			box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.25); } }

	.profile-data {
		@include position(absolute, 0 0 0 0);
		@include size(inherit);
		overflow: hidden;
		padding: 10px 0 0;
		text-align: center;
		text-transform: capitalize;
		font-size: rem(14);
		line-height: rem(24);
		color: #fff;
		background: rgba(0,0,0,0.70);
		border-radius: 50%;
		transform: scale(0,0);
		transition: all .4s;
		span {
			display: block; }

		.media-data {
			padding-bottom: 10px;
			.profile-media {
				display: inline-block; } }

		.profile-write {
			@include size(100%);
			background: $accent-color;
			color: #fff;
			font-size: rem(18);
			text-align: center;
			padding: 15px 0; } } }

.section-features {
	padding: 70px 0 65px;
	color: #fff;
	background: {
		color: $main-color;
		image: url('../img/features/features.jpg');
		repeat: no-repeat;
		position: center right;
		size: cover; } }

