@import 'bourbon';
@import 'helpers/variables';

.profile-data {
	background: #fff;
	box-shadow: $block-shadow;
	padding: 20px 30px;

	.profile-data-name {
		font-size: rem(30);
		line-height: rem(30px);
		color: $lightgrey;
		margin-bottom: 20px;

		.is-online {
			font-size: rem(18); }

		.is-ofline {
			font-size: rem(18); } }

	.profile-data-location {
		color: $lightgrey;
		font-size: rem(14);
		line-height: rem(30px);
		margin-bottom: 20px;

		.left-icon {
			@include size(44px 25px);
			background: {
				image: url('../img/sprites/icons.png');
				repeat: no-repeat;
				position: 0px 8px; } } } }

.personal-data {
	padding: 30px 0;
	border-top: 1px solid $border-color;
	font-size: rem(14);
	line-height: rem(14px);

	.personal-info {
		padding-top: 13px;
		color: $lightengrey;

		.label {
			width: 165px;
			float: left; }

		.labeled {
			color: $lightgrey;
			overflow: hidden;
			text-transform: capitalize; } } }
