@import 'bourbon';
@import 'helpers/variables';

.profile-avatar-wrapper {
	background: #fff;
	box-shadow: $block-shadow;
	margin-bottom: 10px;
	padding: 15px; }

.profile-avatar {
	position: relative;
	margin-bottom: 12px;

	.object-fit-cover {
		width: 100%;
		height: 435px;
		object-position: 75%; }

	.profile-avatar-buttons {
		width: 120px;
		@include position(absolute, null null 0 null);
		background: rgba(255,255,255,0.70);
		border-top-left-radius: 5px;
		border-top-right-radius: 10px;

		.left-icon {
			@include size(40px 40px);
			background: {
				image: url('../img/sprites/icons.png');
				repeat: no-repeat;
				position: 10px 8px; }
			&:hover {
				background-color: #fff; } } } }

.popup-gallery {
	text-align: center;
	overflow: hidden;
	height: 75px;
	li {
		display: inline-block; }
	img {
		width: 83px;
		height: 75px; } }

.profile-video {
	background: #fff;
	box-shadow: $block-shadow;
	padding: 15px; }
